.carousel-item {
    display: flex;
    flex-direction: column;
    opacity: 0;
    height: 300px;  
        &__image {
      width: 150px;
      height: 100%;
    }
  
    &__content {
      padding: 15px;
      background-color: rgba(0, 0, 0, 0.7);
      color: #fff;
      width: 100%;
      box-sizing: border-box;
      border-radius: 15px;
  
      & > * {
        margin: 0;
      }
    }
  
    &--active {
      opacity: 1;
      
    }
  
    &--prev,
    &--next {
      opacity: 0;
      display: none;
        @media screen and (min-width: 767px) {
          opacity: 1;
          display: block;
      }
    }
  }

  .carousel__back {
    height: 10px;
  }
