.carousel-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 320px;
  background: linear-gradient(90deg, rgba(36, 36, 35, 1) 0%, rgba(15, 15, 15, 1) 70%);
  
  
  @media screen and (min-width: 768px) {
    height: 400px;
  }
}

.carousel-items-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 50%;
  width: 100%;
  
  @media screen and (min-width: 768px) {
    width: 100%;
    gap: 50px;
  }

  @media screen and (min-width: 1024px) {
    gap: 150px;
  }
}

.carousel-prev-button,
.carousel-next-button {
  display: flex;
  border: none;
  background-color: transparent;
  color: #fff;
  font-size: 2rem;
  
  @media screen and (min-width: 768px) {
    font-size: 3rem;
  }
}

.carousel-prev-button {
}

  .carousel-next-button {
  }
  
  .carousel-prev-button:focus,
  .carousel-next-button:focus {
    outline: none;
  }
  
  .carousel__section {
    padding: 15px;
    display: flex;
    flex-direction: column;
    text-align: center;
    background: linear-gradient(90deg, rgba(36, 36, 35, 1) 0%, rgba(15, 15, 15, 1) 70%);
    &__title {
        color: white;
        font-family: 'Raleway';
        font-size: 26px;
      }
      &__subtitle {
        opacity: 70%;
        color: #79b3f4;
        font-family: 'Raleway';
        font-size: 18px;
      }
    }
    
    .carousel-buttons {
      display: flex;
    justify-content: space-between;
    align-self: center;
    width: 70%;
    padding: 10px;

    @media screen and (min-width:767px) {
      width: 80%;
    }
    @media screen and (min-width:1024px) {
      width: 85%;
    }
  }

