.modal-window {
    position: fixed;
    background-color: rgba(255, 255, 255, 0.25);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    z-index: 999;
    pointer-events: none;
    transition: all 0.3s;
}

.modal-window-visible {
    pointer-events: all;
    visibility: visible;
    opacity: 1;
}

.modal-window > div {
    width: 70%;
    max-width: 600px;
    max-height: 600px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 2em;
    background: #242423;
    border-radius: 5px;

    @media screen and (min-width: 767px) {
        max-width: 800px;
        max-height: 900px;
    }
}

