.modal-close {
    cursor: pointer;
    height: 32px;
    opacity: .3;
    position: absolute;
    right: 32px;
    top: 32px;
    background-color: transparent;
    width: 32px;
    padding: 0;
    margin: 0;
    border: none;
}
.modal-close:hover {
    opacity: 1;
}
.modal-close:before,
.modal-close:after {
    background-color: white;
    content: " ";
    height: 33px;
    position: absolute;
    top: 0;
    width: 2px;
    left: 50%;
}
.modal-close:before {
    transform: rotate(45deg);
}
.modal-close:after {
    transform: rotate(-45deg);
}

.modal {
    &__title {
        text-align: center;
        font-family: 'Raleway';
        font-size: 24px;
        color: white;
        padding-bottom: 10px;
    }

    &__image {
        width: 100%;
        max-width: 800px;
        max-height: 500px;
        @media screen and (min-width: 767px) {
            max-height: 800px;
        }
    }

    &__description {
        padding-top: 10px;
        text-align: center;
        font-family: 'Raleway' 'sans-serif';
        font-weight: bold;
        font-size: 12px;
        color: white;
    }
}