.header {
  background-image: url("../../assets/header-bg.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 300px;
  display: flex;
  justify-content: center;
  @media screen and (min-width: 767px) {
    height: 450px;
  }

  &__container {
    margin: 90px 0px 0px 0px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: center;
    @media screen and (min-width: 767px) {
      margin: 150px 0px;
      gap: 20px;
    }
  }

  &__title {
    font-family: "Raleway";
    font-weight: 800;
    color: white;
    font-size: 16px;

    @media screen and (min-width: 350px) {
      font-size: 24px;
    }
    @media screen and (min-width: 767px) {
      font-size: 48px;
    }
  }

  &__button {
    background: linear-gradient(60deg, rgba(121,179,244,1) 0%, rgba(25,107,199,1) 80%);
    text-shadow: 1px 1px 5px black;
    opacity: 85%;
    border: black;
    justify-content: center;
    padding: 10px 30px;
    border-radius: 3px;
    text-decoration: none;
    color: white;
    font-family: "Raleway";
    font-weight: 600;

    &:hover {
      opacity: 100%;
      box-shadow: 0px 0px 5px 0px rgb(134, 134, 134);
      transition: all 0.2s;
        scale: 1.01;
        filter: brightness(1.1);
    }
    @media screen and (min-width: 767px) {
      padding: 10px 30px;
      font-size: 20px;
    }
  }
}

.typing {
  width: 100%;
  animation: typing 2s steps(22), blink 0.6s step-end infinite alternate;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}
